@font-face {
    font-family: "Geometria";
    src: url('../fonts/Geometria-Regular.woff2') format('woff2'),
        url('../fonts/Geometria-Regular.woff') format('woff');
    font-weight: 400;
    display: swap;
}

@font-face {
    font-family: "Geometria";
    src: url('../fonts/Geometria-Medium.woff2') format('woff2'),
        url('../fonts/Geometria-Medium.woff') format('woff');
    font-weight: 500;
    display: swap;
}

*,*:before,*:after{
    padding: 0;
    margin: 0;
    border: 0;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 16px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top; max-width: 100%;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
}

body {
    /* font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; */
    font-family: "Geometria";
    color: #333;
}

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}

.header {
    background-color: #222;
    color: #999;
    margin-bottom: 20px;
}

.header__container {
    display: flex;
    align-items: center;
    gap: 30px;
    min-height: 50px;
}

.logo {
    color: inherit;
    font-size: 18px;
    line-height: 1.1;
    flex-shrink: 0;
}

.menu {
    width: 100%;
}

.menu__body {
    display: flex;
}

.menu__list {
    display: flex;
    flex-grow: 1;
    align-items: center;
    gap: 30px;
}

.menu__item:last-child {
    margin-left: auto;
}

.menu__link {
    font-size: 14px;
    color: inherit;
    background-color: transparent;
    transition: color .3s;
}

button.menu__link {
    color: inherit;
}

.menu__link:hover {
    color: #fff;
}

.main__container {
    padding-bottom: 30px;
}

.main__content {
    background-color: #eee;
    color: #333;
    border-radius: 6px;
    padding: 48px 60px;
    margin-bottom: 50px;
}

.title {
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 10px;
}

.main__title {
    font-size: 63px;
}

.main__desc {
    font-size: 21px;
    line-height: 1.4;
}

.main__desc p:not(:last-child) {
    margin-bottom: 20px;
}

.main__desc p:last-child {
    color: #3a87ad;
}

.main__bottom {
    width: 370px;
}

.main__bottom-title {
    font-size: 30px;
}

.btn {
    display: inline-block;
    font-size: 14px;
    padding: 6px 12px;
    border-radius: 4px;
    line-height: 1.42;
    color: #fff;
    transition: background-color .3s;
}

.btn.disable {
    background-color: #bbb;
}

.main__bottom-btn {
    background-color: #d9534f;
}

.main__bottom-btn:hover {
    background-color: #d2322d;
}

.student-login__title,
.admin-login__title {
    margin-bottom: 40px;
}

.student-login__desc,
.admin-login__desc {
    font-size: 18px;
    margin-bottom: 30px;
}

.admin-login .form__label > div {
    width: auto;
}

.form {
    width: 770px;
}

.form__label {
    display: flex;
    align-items: center;
    gap: 30px;
}

.form__label:not(:last-child) {
    margin-bottom: 17px;
}

.form__text {
    width: 110px;
    flex-shrink: 0;
}

.form__label > div {
    width: 100%;
}

.form__input {
    border: 1px solid #bcbcbc;
    border-radius: 5px;
    color: #000;
    font-size: 20px;
    line-height: 1.2;
    max-width: 447px;
    padding: 17px 38px 15px;
    width: 100%;
}

.custom-select__control {
    min-height: 55px !important;
    background: #fff !important;
    font-size: 18px !important;
    border-radius: 8px !important;
    cursor: pointer !important;
}

.custom-select__menu {
    animation: fadeIn .35s ease-in-out;
    z-index: 99 !important;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: scale(0.9);
    }

    50% {
        opacity: 0.3;
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.custom-select__menu-list {
    padding: 20px !important;
}

.custom-select__value-container {
    padding: 0 20px !important;
}

.custom-select__option {
    display: flex !important;
    height: 55px;
    cursor: pointer !important;
    padding: 0 16px !important;
    border-radius: 8px;
    align-items: center;
    background-color: #fff !important;
}

.custom-select__option:not(:last-child) {
    margin-bottom: 16px;
}

.custom-select__multi-value__remove {
    background-color: transparent !important;
    color: #333 !important;
}

.custom-select__option--is-selected,
.custom-select__option:hover {
    background-color: #f2f2f2 !important;
    color: #333 !important;
}

.custom-select__dropdown-indicator svg {
    display: none;
}

.custom-select__dropdown-indicator::after {
    content: '';
    background: url('../img/arrow-down.svg') no-repeat;
    width: 21px;
    height: 21px;
    transform: rotate(0deg);
    z-index: 10;
    transition: .5s;
}

.error {
    border: 1px solid red;
    border-radius: 8px;
}

.form__btn,
.anket-view__start-btn,
.question__next-btn {
    background-color: #10cf50;
}

.form__btn {    
    margin: 30px 0;
}

.anket-view__start-btn {
    margin-bottom: 30px;
}

.form__btn:hover,
.anket-view__start-btn:hover,
.anket-view__start-btn:hover {
    background-color: #0ebe49;
}

.info__title {
    margin-bottom: 30px;
}

.info__subtitle {
    font-size: 24px;
    margin-bottom: 10px;
}

.info__text {
    font-size: 14px;
    line-height: 1.42;
    color: #333333;
    background-color: #fff;
}

.footer {
    font-size: 14px;
    padding: 20px 0;
    color: #333;
    border-top: 1px solid #eee;
}

table {
    border-collapse: collapse;
    margin-bottom: 30px;
    width: 100%;
}

.anket-view__table thead tr {
    background-color: #333;
    color: #fff;
}

.anket-view__table th{
    text-align: left;
}

.anket-view__table th, 
td {
    padding: 8px;
}

td, th {
    border: 1px solid #ddd;
}

.question {
    margin-bottom: 30px;
}

.question__table tr {
    background-color: #f5f5f5;
}

.question__table td {
    width: 50%;
}

.question__table th {
    padding: 10px;
}

.question__table label {
    margin-right: 20px;
}

.question__table label input {
    margin-left: 5px;
}

.question__table input[type="text"] {
    width: 50%;
    border: 1px solid #ddd;
    padding: 5px;
    border-radius: 4px;
}